import { Component, OnInit, Input } from '@angular/core';
import { scrollStart, logScroll } from '../../utils/revealing-header';
import { FavoritesStorage } from './../../storage/favorites.storage';
import { EventsService } from 'src/app/services/events.service';

@Component({
  selector: 'app-favorites',
  templateUrl: './favorites.page.html',
  styleUrls: ['./favorites.page.scss'],
})
export class FavoritesPage implements OnInit {

  @Input('header') header: any;

  subscription: any;
  search: string = "";
  items: any = [];
  result: any = [];
  constructor(public favorites: FavoritesStorage, events: EventsService) {
    this.subscription = events.subscribe(`storage:${favorites.table}`, (data) => {
      console.log('evennnnnnt')
      this._checkState();
    });
  }

  ngOnInit() {
    this.load();
  }

  public async _checkState() {
    const favorites: any = await this.favorites.read();
    this.items = favorites;
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  async load() {
    const items = await this.favorites.read();
    this.items = items;
  }

  scrollStart(header) {
    scrollStart(this, header);
  }

  lastX: any;
  logScroll(event) {
    logScroll(this, event);
  }

  onInput(event: any) {

  }
}
